export default [
    {
        path: "user/List",
        name: "homeUserList",
        component: () => import("@/pages/user/List"),
        meta: ["用户信息", "用户列表"]
    },
    {
        path: "user/Add",
        name: "homeUserAdd",
        component: () => import("@/pages/user/Add"),
        meta: ["用户信息", "添加用户"]
    },

];



