import axios from 'axios'
import pubJson from '../pubJson'
import { Notification, MessageBox } from 'element-ui'
export default {
    install: function (Vue, options) {
        Object.assign(Vue.prototype, {
            http: (record) => {
                //let userInfo = JSON.parse(localStorage.getItem("userInfo") || "{}");
                //兼容跨域服务器
                let uri = record.url.indexOf("http") == 0 ? record.url : pubJson.url + record.url;
                return axios.post(uri, record.data, {
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    },
                    timeout: 6000
                }).then(res => {
                    if (res.code == 200) {
                        return res["data"]
                    } else{
                        Notification.error({
                            title: res.msg
                        })
                    }
                }).catch(err => {
                    Notification.error({
                        title: err
                    })
                })
            },
        })

    }
}



