import pages from '@/pages/router'
export default [
    {
        path: '*',
        component: () => import("@/pages/html/NotFound"),
    },
    {
        path: '/',
        name: 'login',
        component: () => import("@/pages/html/login"),
        meta: { title: "登录" }
    },
    {
        path: '/home',
        name: 'home',
        component: () => import("@/pages/html/home"),
        children: [...pages]
    },
]



